@import '../variables.scss';

.sort {
  position: relative;
  &__label {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    svg {
      margin-right: 8px;
    }

    b {
      margin-right: 8px;
      white-space: nowrap;
    }

    span {
      color: $green;
      border-bottom: 1px dashed $green;
      cursor: pointer;
    }
  }

  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 180px;

    ul {
      overflow: hidden;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $green;
        }
      }
    }
  }

  &__popup {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.09);
  }
}
